<template>
	<view class="like-message-page">
		
		<!-- ÁÇπËµûÂàóË°® -->
		<scroll-view class="like-list" scroll-y>
			<!-- Á©∫Áä∂ÊÄÅ -->
			<view v-if="likeList.length === 0" class="empty-state">
				<text class="empty-icon">üëç</text>
				<text class="empty-text">ÊöÇÊó†ÁÇπËµûÊ∂àÊÅØ</text>
			</view>
			
			<!-- ÁÇπËµûÈ°π -->
			<view 
				v-for="(like, index) in likeList" 
				:key="index" 
				class="like-item"
				@click="goToPostDetail(like)"
			>
				<!-- Â∑¶‰æßÂ§¥ÂÉè -->
				<image 
					class="avatar" 
					:src="like.avatar || '/static/default-avatar.png'" 
					mode="aspectFill"
				></image>
				
				<!-- Âè≥‰æßÂÜÖÂÆπÂå∫Âüü -->
				<view class="like-content">
					<!-- Áî®Êà∑ÊòµÁß∞ -->
					<text class="nickname">{{ like.nickname }}</text>
					
					<!-- Êó∂Èó¥ÂíåÊìç‰Ωú -->
					<view class="time-row">
						<text class="time">{{ formatTime(like.created_at) }}</text>
						<text class="like-type">Ëµû‰∫Ü‰Ω†ÁöÑÂ∏ñÂ≠ê</text>
					</view>
					
					<!-- ÂéüÂ∏ñÂ≠êÂÜÖÂÆπÈ¢ÑËßà -->
					<view class="post-preview" v-if="like.post_content">
						<text class="preview-label">‰Ω†ÁöÑÂ∏ñÂ≠êÔºö</text>
						<text class="preview-text">{{ like.post_content }}</text>
						<!-- Â∏ñÂ≠êÂõæÁâáÈ¢ÑËßà -->
						<image 
							v-if="getFirstImage(like.post_images)" 
							class="preview-image" 
							:src="getFirstImage(like.post_images)" 
							mode="aspectFill"
						></image>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import { mapState } from 'vuex'
	
	export default {
		data() {
			return {
				likeList: []
			};
		},
		
		computed: {
			...mapState('m_user', ['openid', 'token'])
		},
		
		methods: {
			// ËøîÂõû‰∏ä‰∏ÄÈ°µ
			goBack() {
				uni.navigateBack();
			},
			
			// Ë∑≥ËΩ¨Âà∞Â∏ñÂ≠êËØ¶ÊÉÖÈ°µ
			goToPostDetail(like) {
				const images = this.parseImages(like.post_images);
				
				if (images && images.length > 0) {
					uni.navigateTo({
						url: '/subpkg/topic-detail/topic-detail?id=' + like.post_id
					});
				} else {
					uni.navigateTo({
						url: '/subpkg/post-detail/post-detail?id=' + like.post_id
					});
				}
			},
			
			// Ê†ºÂºèÂåñÊó∂Èó¥
			formatTime(timeString) {
				const now = new Date();
				const time = new Date(timeString);
				const diff = Math.floor((now - time) / 1000); // Áßí
				
				if (diff < 60) {
					return 'ÂàöÂàö';
				} else if (diff < 3600) {
					return Math.floor(diff / 60) + 'ÂàÜÈíüÂâç';
				} else if (diff < 86400) {
					return Math.floor(diff / 3600) + 'Â∞èÊó∂Ââç';
				} else if (diff < 604800) {
					return Math.floor(diff / 86400) + 'Â§©Ââç';
				} else {
					const year = time.getFullYear();
					const month = String(time.getMonth() + 1).padStart(2, '0');
					const day = String(time.getDate()).padStart(2, '0');
					return `${year}-${month}-${day}`;
				}
			},
			
			// Ëß£ÊûêÂõæÁâá JSON
			parseImages(imagesString) {
				if (!imagesString) return [];
				try {
					return JSON.parse(imagesString);
				} catch (e) {
					return [];
				}
			},
			
			// Ëé∑ÂèñÂ∏ñÂ≠êÁ¨¨‰∏ÄÂº†ÂõæÁâá
			getFirstImage(imagesString) {
				const images = this.parseImages(imagesString);
				return images && images.length > 0 ? images[0] : '';
			},
			
			// Ëé∑ÂèñÁÇπËµûÊ∂àÊÅØÂàóË°®
			async getLikeMessages() {
				try {
					uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
					
					const { data: res } = await uni.$http.get('/users/like-message', {
						openid: this.openid
					});
					
					uni.hideLoading();
					
					if (res.meta.status === 200) {
						this.likeList = res.message;
					} else {
						uni.showToast({
							title: res.meta.msg || 'Ëé∑ÂèñÂ§±Ë¥•',
							icon: 'none'
						});
					}
				} catch (error) {
					uni.hideLoading();
					console.error('Ëé∑ÂèñÁÇπËµûÊ∂àÊÅØÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'ÁΩëÁªúÈîôËØØ',
						icon: 'none'
					});
				}
			}
		},
		
		onLoad() {
			// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
			if (!this.token) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				});
				setTimeout(() => {
					uni.navigateBack();
				}, 1500);
				return;
			}
			
			// Ëé∑ÂèñÁÇπËµûÊ∂àÊÅØ
			this.getLikeMessages();
		}
	}
</script>

